___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  position: relative;
  z-index: 2;
  font-family: "Roboto", sans-serif;
  background: #ccc;
}

h1,
.wrapper-title,
.letter {
  font-family: "Poppins", sans-serif;
}

button {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  cursor: pointer;
  font-size: clamp(20px, 3vw, 24px);
  font-weight: 700;
  background: lightblue;
  margin-bottom: 2rem;
}
button:active {
  transform: translateY(2px);
}

.wrapper-container {
  background: rgba(0, 0, 0, 0.8);
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  height: 100%;
  width: 100vw;
}
.wrapper-container .wrapper {
  background: #ccc;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  min-height: 200px;
  width: clamp(100px, 90%, 500px);
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper-container .wrapper-title {
  font-size: clamp(22px, 5.5vw, 28px);
  text-align: center;
  margin: 1rem 0;
}
.wrapper-container .wrapper-text {
  font-size: clamp(14px, 3vw, 18px);
  text-align: center;
  margin: 0.5rem 1rem 2rem;
}

main {
  height: 100vh;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  background: white;
  border-radius: 10px;
}
main .title-container {
  text-align: center;
  padding: 1rem 0 2rem;
  display: flex;
  flex-direction: column;
}
main .title-container h1 {
  font-size: clamp(28px, 5vw, 38px);
}
main .title-container .score {
  font-size: clamp(20px, 5vw, 28px);
}
main .img-container {
  margin: 0 auto;
  height: 350px;
  width: 300px;
}
main .img-container img {
  height: 100%;
  width: 100%;
  object-fit: contain;
}
main .word-container .word {
  margin: 1rem auto;
  display: flex;
  flex-wrap: wrap;
}
main .word-container .word .letter {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.7rem;
  font-weight: bold;
  margin: 1rem;
  background: black;
  color: white;
}
main .tested-letters-container,
main .word-container {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100px;
  justify-content: space-between;
  align-items: center;
}
main .tested-letters {
  display: flex;
  flex-wrap: wrap;
}
main .tested-letters .tested-letter {
  margin: 1rem;
  font-size: 1.5rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.scss"],"names":[],"mappings":"AAEA;EACE,SAAA;EACA,UAAA;EACA,sBAAA;AAAF;;AAGA;EACE,kBAAA;EACA,UAAA;EACA,iCAAA;EACA,gBAAA;AAAF;;AAGA;;;EAGE,kCAAA;AAAF;;AAGA;EACE,oBAAA;EACA,YAAA;EACA,mBAAA;EACA,gFAAA;EAEA,eAAA;EACA,iCAAA;EACA,gBAAA;EACA,qBAAA;EACA,mBAAA;AADF;AAEE;EACE,0BAAA;AAAJ;;AAIA;EACE,8BAAA;EACA,kBAAA;EACA,OAAA;EACA,MAAA;EACA,UAAA;EACA,YAAA;EACA,YAAA;AADF;AAEE;EACE,gBAAA;EACA,mBAAA;EACA,gFAAA;EAEA,kBAAA;EACA,QAAA;EACA,SAAA;EACA,iBAAA;EACA,+BAAA;EACA,gCAAA;EACA,aAAA;EACA,sBAAA;EACA,mBAAA;AADJ;AAEI;EACE,mCAAA;EACA,kBAAA;EACA,cAAA;AAAN;AAEI;EACE,iCAAA;EACA,kBAAA;EACA,wBAAA;AAAN;;AAKA;EACE,aAAA;EACA,WAAA;EACA,iBAAA;EACA,cAAA;EACA,kBAAA;EACA,iBAAA;EACA,mBAAA;AAFF;AAGE;EACE,kBAAA;EACA,oBAAA;EACA,aAAA;EACA,sBAAA;AADJ;AAEI;EACE,iCAAA;AAAN;AAEI;EACE,iCAAA;AAAN;AAGE;EACE,cAAA;EACA,aAAA;EACA,YAAA;AADJ;AAEI;EACE,YAAA;EACA,WAAA;EACA,mBAAA;AAAN;AAII;EACE,iBAAA;EACA,aAAA;EACA,eAAA;AAFN;AAGM;EACE,WAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,iBAAA;EACA,iBAAA;EACA,YAAA;EACA,iBAAA;EACA,YAAA;AADR;AAME;;EAEE,aAAA;EACA,aAAA;EACA,sBAAA;EACA,WAAA;EACA,iBAAA;EACA,8BAAA;EACA,mBAAA;AAJJ;AAOE;EACE,aAAA;EACA,eAAA;AALJ;AAMI;EACE,YAAA;EACA,iBAAA;AAJN","sourcesContent":["@import \"~simple-keyboard/build/css/index.css\";\r\n\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n  position: relative;\r\n  z-index: 2;\r\n  font-family: \"Roboto\", sans-serif;\r\n  background: #ccc;\r\n}\r\n\r\nh1,\r\n.wrapper-title,\r\n.letter {\r\n  font-family: \"Poppins\", sans-serif;\r\n}\r\n\r\nbutton {\r\n  padding: 0.5rem 1rem;\r\n  border: none;\r\n  border-radius: 10px;\r\n  box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px,\r\n    rgba(0, 0, 0, 0.22) 0px 10px 10px;\r\n  cursor: pointer;\r\n  font-size: clamp(20px, 3vw, 24px);\r\n  font-weight: 700;\r\n  background: lightblue;\r\n  margin-bottom: 2rem;\r\n  &:active {\r\n    transform: translateY(2px);\r\n  }\r\n}\r\n\r\n.wrapper-container {\r\n  background: rgb